import React from 'react';

const BtmCont = () => {
  return (
    <div className='btm-cont'>
      <div className='container'>
        <aside>
          <div className='logo-out'>Lloyds</div>
          <div className='logo-center-out'>Agile</div>
          <a className='scroll-to' href='#scroll-home-1'>Scroll To</a>
        </aside>
      </div>
    </div>
  );
};

export default BtmCont;


