import ChooseCover from 'assets/images/logo-choose-your-cover.png';
import ChooseCoverHover from 'assets/images/logo-choose-your-cover-hover.png';
import ChooseCoverText from 'assets/images/brands-text-choose-your-cover.svg';
import IncidentRes from 'assets/images/logo-our-approach.png';
import IncidentResHover from 'assets/images/logo-our-approach-hover.png';
