import Whats from 'assets/images/logo-whats-covered.png';
import WhatsHover from 'assets/images/logo-whats-covered-hover.png';
import WhatsText from 'assets/images/brands-text-whats-covered.svg';

const productLinkDatas = [{
  id: 1,
  link: '/get-covered',
  classname: '',
  liclassname: '',
  alt: 'Choose your cover',
  image1: ChooseCover,
  image2: ChooseCoverHover,
  image3: ChooseCoverText
},{
  id: 2,
  link: '#scroll-home-1',
  classname: 'scroll-to',
  liclassname: 'our-approach',
  alt: 'Incident Response',
  image1: IncidentRes,
  image2: IncidentResHover,
  image3: IncidentResText
},{
  id: 3,
  link: '#scroll-home-2',
  classname: 'scroll-to',
  liclassname: 'our-approach',
  alt: 'What is Covered',
  image1: Whats,
  image2: WhatsHover,
  image3: WhatsText
}]

export default productLinkDatas;


