import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { DefaultPlayer as Video } from 'react-html5video';
import CybercareVideo from 'assets/images/AdobeStock_137701706.mov';
import productLinkDatas from './ProductLinkDatas';
import BtmCont from 'components/main/BtmCont';
import './Paragraph_1.scss';

class Paragraph_1 extends Component {
  render() {
    return (
      <div className="banner-main para-cont1">
        <div className='banner-bg-out'>
          <Video autoPlay className='hero-video' id='hero-video' controls={null} loop muted playsInline preload='auto'>
            <source src={CybercareVideo} type='video/mp4' />
          </Video>
          <div className='banner-cont'>
            <div className='container'>
              <aside>
                <h1>Cyber threat protection</h1>
                <p className='lrg'>
                  CyberCare insurance is tailored to protect small business with essential 
                  coverage against cyber threats. It offers sophisticated yet affordable 
                  coverage to protect your business reputation.
                </p>
                <div className='btn-out'>
                  <a className='btn' href='/get-covered'>Choose your cover</a>
                </div>
              </aside>
            </div>
          </div>
          <div className='product-links'>
            <div className='container'>
              <ul>
                {
                  productLinkDatas.map((plData, i) =>
                    <ProductLinks
                      key={plData.id}
                      link={plData.link}
                      classname={plData.classname}
                      liclassname={plData.liclassname}
                      alt={plData.alt}
                      image1={plData.image1}
                      image2={plData.image2}
                      image3={plData.image3}
                    />
                  )
                }                
              </ul>
            </div>
          </div>
          <BtmCont />
        </div>
      </div>
    );
  }
}

const ProductLinks = ({link, classname, liclassname, alt, image1, image2, image3}) => {
  return (
    <li className={liclassname}>
      <Link to={link} className={classname}>
        <div className='icon-out'>
          <img alt={alt} className="img1" src={image1}/>
          <img alt={alt} className="img2" src={image2}/>
        </div>
        <div className='txt'>
          <img alt={alt} src={image3} />
        </div>
      </Link>
    </li>
  );
};

export default Paragraph_1;


