import React, { Component } from 'react';
import Header from 'components/header/Header';
import CyberApp from 'assets/images/animation-01.png';
import BtmCont from 'components/main/BtmCont';
import './Paragraph_2.scss';

class Paragraph_2 extends Component {
  render() {
    return (
      <div className='split-cont cont1' id='scroll-home-1'>

        <Header whiteClass='header-white' />

        <div className='container'>
          <div className='article'>
            <aside>
              <h2 className='lrg'>The CyberCare Approach</h2>
              <p>The first respondent is an IT Security specialist tasked with managing the incident, diagnosing and fixing
                the issue to get your business back on track. IT forensic experts get in touch as soon as practical to provide
                triage and remediation around the clock.</p>
              <p>This is followed up with a pre-agreed panel of experts  IT forensics, legal and public relations  to manage
                the crisis, all local and coordinated together.</p>
              <p>Youll get the very best help when you need it most.</p>
            </aside>
            <figure>
              <img alt="The CyberCare Approach" src={CyberApp} />
            </figure>
          </div>
          <BtmCont />
        </div> 
      </div>
    );
  }
}

export default Paragraph_2;


