import React from 'react';
import { Link } from 'react-router-dom';
import './Header.scss';
import Logo from 'assets/images/brand-cybercare.svg';
import LogoWhite from 'assets/images/brand-cybercare-white.svg';

const Header = ({ whiteClass }) => {
  return (
    <header className={`header-main ${whiteClass ? whiteClass : '' }`}>
        <div className='container'>
          <div className='brand'>
            <Link to='/' title='CyberCare'>
              <img alt="CyberCare" src={whiteClass === 'header-white' ? LogoWhite: Logo } />
            </Link>
          </div>
          <div className='nav-bar'>
            <nav className='collapse-menu'>
              <ul className='nav'>
                <li>
                  <Link to='/'>Home</Link>
                </li>
                <li>
                  <Link to='/get-covered'>Get covered</Link>
                </li>
                <li>
                  <Link to='/how-to-claim'>How to claim</Link>
                </li>
                <li>
                  <Link to='https://cyberclaims.agileunderwriting.com/hc/en-us' target='_new'>Support &amp; FAQs</Link>
                </li>                
              </ul>
              {/* <div className='close-btn'>Close Menu</div> */}
            </nav>
            <div className='menu-btn'>
              <div className='icon-bar'>
                <span></span>
              </div>
            </div>
          </div>
        </div>
      </header>
  );
};

export default Header;


