import React, { Component } from 'react';
import Header from 'components/header/Header';
import BtmCont from 'components/main/BtmCont';
import articleDatas from './ArticleDatas';
import Articles from './Articles';
import './Paragraph_3.scss';

class Paragraph_3 extends Component {
  render() {
    return (
      <div className='feature-list-out' id='scroll-home-2'>

        <Header whiteClass='header-white2'/>

        <div className='feature-list'>
          <div className='container'>
            <div className='heading-txt'>
              <h2 className='lrg'>What we cover</h2>
              <aside>
                <p className='lrg'>CyberCare provides a comprehensive range of coverage to minimise the impact to your business, and uphold
                  your reputation.</p>
                <p>
                  Be sure to refer to your
                  <a href='https://buy.cybercare.com.au/CyberCare_PDS.pdf'>Policy Wording</a>
                  to understand your cover.
                </p>
              </aside>
            </div>
            <div className='article-out'>
              {
                articleDatas.map((aData, i) => 
                  <Articles 
                    title={aData.title}
                    desc={aData.desc}
                    image={aData.image}
                    key={aData.id}
                  />
                )  
              }
            </div>
            <div className='btm-heading'>
              <h2>Choose your cover</h2>
              <div className='btn-out'>
                <a className='btn' href='/get-covered'>Choose your cover</a>
              </div>
            </div>
            <BtmCont />
          </div>
        </div>
      </div>
    );
  }
}

export default Paragraph_3;


