import React from 'react';
import './PageTemplate.scss';

const PageTemplate = ({header, children, footer}) => {
  return (
    <div className="container-main">
      {header}
      <main className="main-content">
        {children}
      </main>
      {footer}
    </div>
  );
};

export default PageTemplate;


