import React from 'react';
import { Route, Switch } from 'react-router-dom';
import { Home, GetCovered } from 'pages';

const App = () => {
  return (
    <React.Fragment>
      <Switch>
        <Route exact path="/" component={Home} />
        <Route path="/get-covered" component={GetCovered} />
      </Switch>
    </React.Fragment>
  );
};

export default App;


